import React from 'react'
 import Sdata from './Sdata'
 import Card from "./Card"
 


const Project = () => {
    return (
        <>
         <div id="wrapperr">
            <div className="bubble X1"></div>
            <div className="bubble X2"></div>
            <div className="bubble X3"></div>
            <div className="bubble X4"></div>
            <div className="bubble X5"></div>
            <div className="bubble X6"></div>
            <div className="bubble X7"></div>
            <div className="bubble X8"></div> 
            <div className="bubble X9"></div>
            <div className="bubble X10"></div>
            <div className="bubble X12"></div>
            <div className="bubble X12"></div>
            <div className="bubble X12"></div>
            <div className="bubble X1"></div>
            <div className="bubble X15"></div>
            <div className="bubble X16"></div>
            <div className="bubble X17"></div>
            <div className="bubble X18"></div>
            <div className="bubble X19"></div>
        </div> 
        
          <section id="myproject" className="project">
        
        <div className="my-4">
            <h1 className="work  text-center">Project</h1>
        

        </div>
        
        <div className="container-fluid mb-5">
            <div className="row">
                <div className="col-10 mx-auto">
                    <div className="row gy-4">
                    {
                    Sdata.map((val, ind) => {
                        return <Card key={ind} imgsrc={val.imgsrc} title={val.title}/>
                    })
                }
                    </div>
                </div>
            </div>
        </div>
        
        </section>
     </>
    )
}

export default Project;
